export default function ProductCoilSpecsSection() {
  const specifications = [
    {
      category: "Thickness Range",
      value: "0.3mm - 6.0mm"
    },
    {
      category: "Width Range",
      value: "10mm - 1500mm"
    },
    {
      category: "Standard Grades",
      value: "200, 300, 400 Series"
    },
    {
      category: "Surface Finish",
      value: "2B, BA, No.4, 8K Mirror"
    },
    {
      category: "Tensile Strength",
      value: "500-900 MPa"
    },
    {
      category: "Delivery Time",
      value: "15-30 days"
    }
  ]

  return (
    <section className="py-16 md:py-24 bg-gray-50">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <h2 className="text-4xl md:text-5xl font-bold text-gray-900 mb-12 text-center">
          Product Specifications
        </h2>
        <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-6">
          {specifications.map((spec, index) => (
            <div key={index} className="bg-white p-6 rounded-lg shadow-md">
              <h3 className="text-lg font-semibold text-gray-900 mb-2">{spec.category}</h3>
              <p className="text-gray-600 text-xl">{spec.value}</p>
            </div>
          ))}
        </div>
      </div>
    </section>
  )
}

